(function() {
  angular.module("coolnameFrontend").factory("UserService", [
    "Restangular", function(Restangular) {
      var model;
      model = "users";
      return {
        getUser: function(userId) {
          return Restangular.one(model, userId).get();
        },
        updateUser: function(user) {
          return user.put();
        }
      };
    }
  ]);

}).call(this);



(function() {
  angular.module("coolnameFrontend").factory("WordService", [
    "Restangular", function(Retangular) {
      var model;
      model = "words";
      return {
        listWords: function(userId) {
          return Restangular.one("users", userId).getList(model);
        },
        getWord: function(userId, wordId) {
          return Restangular.one("users", userId).one(model, wordId).get();
        },
        updateWord: function(word) {
          return word.put();
        }
      };
    }
  ]);

}).call(this);



(function() {
  angular.module("coolnameFrontend").factory("RecommendService", [
    "Restangular", function(Retangular) {
      var model;
      model = "recommends";
      return {
        listWords: function(userId) {
          return Restangular.one("users", userId).getList(model);
        },
        getWord: function(userId, wordId) {
          return Restangular.one("users", userId).one(model, wordId);
        },
        updateWord: function(word) {
          return word.put();
        }
      };
    }
  ]);

}).call(this);



(function() {
  var BottomSheetController, DialogController, InputDialogController, app;

  app = angular.module('coolnameFrontend');

  app.controller('MainController', [
    "$scope", "$timeout", "Restangular", "$mdDialog", "$mdMedia", "$mdToast", "$mdBottomSheet", "$speechRecognition", "$cordovaCamera", "$rootScope", function($scope, $timeout, Restangular, $mdDialog, $mdMedia, $mdToast, $mdBottomSheet, $speechRecognition, $cordovaCamera, $rootScope) {
      var leftWords, maxWords, resetSpeechInput, rightWords;
      maxWords = 2;
      leftWords = [];
      rightWords = [];
      $scope.listening = false;
      $scope.speechInput = [];
      $scope.ocrInput = [];
      $scope.customFullscreen = $mdMedia('xs') || $mdMedia('sm');
      $scope.words = [
        {
          word: "Potato",
          definition: "The best vegetable"
        }, {
          word: "Broccoli",
          definition: "The worst vegetable"
        }
      ];
      $scope.currIndex = 0;
      $scope.word = $scope.words[$scope.currIndex];
      Restangular.all("related").getList().then(function(words) {
        $scope.currIndex = 0;
        $scope.words = words;
        maxWords = $scope.words.length;
        return $scope.word = words[$scope.currIndex];
      });
      Restangular.all("words").getList().then(function(words) {
        return $scope.learnedWords = words;
      });
      $scope.isCurrentIndex = function(index) {
        return index === $scope.currIndex;
      };
      $scope.addWord = function(word) {
        Restangular.all("words").post(word);
        return Restangular.all("related").post(word).then(function(words) {
          $scope.currIndex = 0;
          $scope.words = words;
          maxWords = $scope.words.length;
          return $scope.word = words[$scope.currIndex];
        });
      };
      $scope.saveWord = function() {
        $scope.currIndex++;
        console.log($scope.currIndex);
        rightWords.push($scope.word.word);
        if ($scope.currIndex > maxWords - 1) {
          Restangular.all("related").getList().then(function(words) {
            console.log("Words reloaded");
            $scope.currIndex = 0;
            $scope.words = words;
            maxWords = $scope.words.length;
            return $scope.word = words[$scope.currIndex];
          });
        } else {
          $scope.word = $scope.words[$scope.currIndex];
        }
        console.log("Word saved");
        $mdToast.show({
          controller: "",
          templateUrl: "app/main/toast.html",
          template: "Word Added!",
          hideDelay: 2000,
          position: "top right"
        });
        console.dir(rightWords);
        if (rightWords.length > 9) {
          console.log("Posting words");
          Restangular.all("words").all("acceptedWords").customPOST(rightWords).then(function() {});
          return rightWords = [];
        }
      };
      $scope.removeWord = function() {
        $scope.currIndex++;
        console.log($scope.currIndex);
        leftWords.push($scope.word.word);
        if ($scope.currIndex > maxWords - 1) {
          Restangular.all("related").getList().then(function(words) {
            console.log("Words reloaded");
            maxWords = $scope.words.length;
            $scope.currIndex = 0;
            $scope.words = words;
            return $scope.word = $scope.words[$scope.currIndex];
          });
        } else {
          $scope.word = $scope.words[$scope.currIndex];
        }
        console.log("Word rejected");
        console.dir(leftWords);
        if (leftWords.length > 9) {
          console.log("Posting words");
          Restangular.all("words").all("discardedWords").customPOST(leftWords).then(function() {});
          return leftWords = [];
        }
      };
      $scope.openDef = function(ev) {
        var useFullScreen;
        useFullScreen = ($mdMedia('sm') || $mdMedia('xs')) && $scope.customFullscreen;
        return Restangular.all("words").all("translation").customPOST({
          "destLang": "es",
          "word": $scope.word.word
        }).then(function(translation) {
          $scope.word.definition = translation;
          return $mdDialog.show({
            controller: DialogController,
            templateUrl: "app/main/definition.html",
            parent: angular.element(document.body),
            targetEvent: ev,
            clickOutsideToClose: true,
            fullscreen: useFullScreen,
            locals: {
              name: $scope.word.word,
              definition: $scope.word.definition
            }
          });
        });
      };
      $scope.openInput = function(ev) {
        var useFullScreen;
        useFullScreen = ($mdMedia('sm') || $mdMedia('xs')) && $scope.customFullscreen;
        return $mdDialog.show({
          controller: InputDialogController,
          templateUrl: "app/main/inputword.html",
          parent: angular.element(document.body),
          targetEvent: ev,
          clickOutsideToClose: true,
          fullscreen: useFullScreen
        }).then(function(word) {
          console.log(word);
          return $scope.addWord(word);
        }, function() {
          return console.log("dialog closed");
        });
      };
      $scope.showBottomSheet = function($event) {
        return $mdBottomSheet.show({
          templateUrl: 'app/main/learnedwords.html',
          controller: BottomSheetController,
          targetEvent: $event,
          locals: {
            words: $scope.learnedWords
          }
        }).then(function(word) {
          $scope.words = [];
          $scope.currIndex = 0;
          $scope.words.push(word);
          return maxWords = 1;
        });
      };
      document.addEventListener('deviceready', function() {
        return console.log("Device ready", false);
      });
      resetSpeechInput = function() {
        return $scope.listening = false;
      };
      $scope.toggleSpeechInput = function() {
        if ($scope.listening) {
          resetSpeechInput();
          console.log($scope.speechInput);
          return Restangular.all("related").all("multipleWords").customPOST($scope.speechInput).then(function(words) {
            console.log("Words reloaded");
            maxWords = $scope.words.length;
            $scope.currIndex = 0;
            $scope.words = words;
            $scope.word = $scope.words[$scope.currIndex];
            Restangular.all("words").all("translation").customPOST({
              "destLang": "es",
              "word": $scope.word.word
            }).then(function(translation) {
              return $scope.word.definition = translation;
            });
            return $scope.speechInput = [];
          });
        } else {
          $scope.listening = true;
          return window.plugins.speechrecognizer.startRecognize((function(result) {
            var i, len, tmpArray, word;
            resetSpeechInput();
            tmpArray = result[0].split(" ");
            for (i = 0, len = tmpArray.length; i < len; i++) {
              word = tmpArray[i];
              $scope.speechInput.push(word);
            }
            return Restangular.all("related").all("multipleWords").customPOST($scope.speechInput).then(function(words) {
              console.log("Words reloaded");
              maxWords = $scope.words.length;
              $scope.currIndex = 0;
              $scope.words = words;
              $scope.word = $scope.words[$scope.currIndex];
              Restangular.all("words").all("translation").customPOST({
                "destLang": "es",
                "word": $scope.word.word
              }).then(function(translation) {
                return $scope.word.definition = translation;
              });
              return $scope.speechInput = [];
            });
          }), (function(err) {
            resetSpeechInput();
            return console.log(err);
          }), 5);
        }
      };
      $speechRecognition.onUtterance(function(utterance) {
        var i, len, tmpArray, word;
        tmpArray = utterance.split(" ");
        for (i = 0, len = tmpArray.length; i < len; i++) {
          word = tmpArray[i];
          $scope.speechInput.push(word);
        }
        console.dir($scope.speechInput);
        return $rootScope.$broadcast('finishedSpeechProcessing');
      });
      return $scope.takePictureInput = function() {
        console.log("Taking photo");
        return document.addEventListener("deviceready", function() {
          var options;
          console.log("Device ready");
          options = {
            quality: 50,
            destinationType: Camera.DestinationType.DATA_URL,
            sourceType: Camera.PictureSourceType.CAMERA,
            allowEdit: true,
            encodingType: Camera.EncodingType.JPEG,
            targetWidth: 100,
            targetHeight: 100,
            popoverOptions: CameraPopoverOptions,
            saveToPhotoAlbum: false,
            correctOrientation: true
          };
          return $cordovaCamera.getPicture(options).then(function(imageData) {
            var inputString, inputWords;
            inputString = OCRAD(c);
            inputString = inputString.replace(/[.,\/#!$%\^&\*;:{}=\-_`~()]/g, "");
            inputWords = inputString.split(" ");
            $scope.ocrInput = inputWords;
            console.dir($scope.ocrInput);
            return Restangular.all("related").all("multipleWords").customPOST($scope.ocrInput).then(function(words) {
              console.log("Words reloaded");
              maxWords = $scope.words.length;
              $scope.currIndex = 0;
              $scope.words = words;
              $scope.word = $scope.words[$scope.currIndex];
              Restangular.all("words").all("translation").customPOST({
                "destLang": "es",
                "word": $scope.word.word
              }).then(function(translation) {
                return $scope.word.definition = translation;
              });
              return $scope.ocrInput = [];
            });
          }, function(err) {
            return console.log(err);
          });
        });
      };
    }
  ]);

  app.animation(".slide-animation", [
    "$window", function($window) {
      return {
        load: function(element, done) {
          var scope;
          scope = element.scope();
          return TweenMax.to(element, 1, {
            opacity: 1
          });
        },
        beforeAddClass: function(element, className, done) {
          var finishPoint, scope;
          scope = element.scope();
          if (className === 'ng-hide') {
            console.log(scope.direction);
            finishPoint = element.parent().width();
            if (scope.direction === 'left') {
              console.log("reverse finish");
              finishPoint = -finishPoint;
            }
            console.log(finishPoint);
            TweenMax.to(element, 1, {
              left: finishPoint,
              onComplete: done,
              opacity: 0
            });
          } else {
            done();
          }
        },
        removeClass: function(element, className, done) {
          var currAnimation, scope, startPoint;
          scope = element.scope();
          if (className === 'ng-hide') {
            element.removeClass('ng-hide');
            console.log(scope.direction);
            startPoint = element.parent().width();
            if (scope.direction === 'right') {
              console.log("reverse start");
              startPoint = -startPoint;
            }
            console.log(startPoint);
            TweenMax.set(element, {
              left: startPoint
            });
            currAnimation = TweenMax.to(element, 1, {
              left: 0,
              onComplete: done,
              opacity: 1
            });
            console.log(currAnimation.delay());
          } else {
            done();
          }
        }
      };
    }
  ]);

  DialogController = [
    "$scope", "$mdDialog", "name", "definition", function($scope, $mdDialog, name, definition) {
      $scope.name = name;
      $scope.definition = definition;
      return $scope.hide = function() {
        return $mdDialog.hide();
      };
    }
  ];

  InputDialogController = [
    "$scope", "$mdDialog", function($scope, $mdDialog) {
      $scope.word = "";
      $scope.cancel = function() {
        console.log("Cancel clicked");
        return $mdDialog.cancel();
      };
      $scope.answer = function() {
        return $mdDialog.hide($scope.word);
      };
      return $scope.hide = function() {
        return $mdDialog.hide();
      };
    }
  ];

  BottomSheetController = [
    "$scope", "$mdBottomSheet", "words", function($scope, $mdBottomSheet, words) {
      $scope.words = words;
      return $scope.select = function($index) {
        return $mdBottomSheet.hide($scope.words[$index]);
      };
    }
  ];

}).call(this);



